import React, { Component } from "react";
import STMain from "../Home/images/ST/1.png";
import { withTranslation } from "react-i18next";
import reactHtmlParser from 'react-html-parser';

export class savetax extends Component {
  render() {
    const { t } = this.props;
    return (
      <main>
        <div className="container container-wrapper">
          <div className="row">
            <div className="col-lg-7 col-md-6 mb-4">
              <h2>{t("ST:ST1")}</h2>
              <p>{t("ST:ST2")}</p>
              <div className="registerFair">
                <a
                  href="https://app.nivesh.com/home/index"
                  target="_blank"
                  title=""
                  className="btn btn-primary"
                >
                  {t('CTAFooter:InvestNow1')}
                </a>
              </div>
            </div>
            <div className="col-lg-5 col-md-6 mb-4">
              <img src={STMain} className="img-fluid" />
            </div>
          </div>
        </div>

        <div className="landingPlatforms">
          <div className="container">
            <div className="row">
              <h2>{t("ST:ST3")}</h2>
              <p>
                {t("ST:ST4")}
                <br />
                <br />
                {reactHtmlParser(t("ST:ST5"))}
              </p>
            </div>
          </div>
        </div>

        <div className="container container-wrapper">
          <div className="row">
            <div className="col-lg-12">
              <h2>{t("ST:ST6")}</h2>
              <p>{t("ST:ST7")}</p>
            </div>
            <div className="col-lg-12">
              <h2>{t("ST:ST8")}</h2>
              <p>{t("ST:ST9")}</p>
              <p>{t("ST:ST10")}</p>
              <p>{t("ST:ST11")}</p>
            </div>
            <div className="col-lg-12">
              <h2>{t("ST:ST12")}</h2>
              <p>{t("ST:ST13")}</p>
              <p>{t("ST:ST14")}</p>
            </div>
            <div className="col-lg-12">
              <h2> {t("ST:ST15")}</h2>
              <table className="table custom-table">
                <thead className="thead-blue">
                  <tr>
                    <th scope="col">{t("ST:ST16")}</th>
                    <th scope="col">{t("ST:ST17")} </th>
                    <th scope="col">{t("ST:ST18")}</th>
                  </tr>
                </thead>
                <tbody>
                  <tr>
                    <th scope="row">{t("ST:ST19")}</th>
                    <td>15 years</td>
                    <td>8%</td>
                  </tr>
                  <tr>
                    <th scope="row">{t("ST:ST20")}</th>
                    <td>6 years</td>
                    <td>8%</td>
                  </tr>
                  <tr>
                    <th scope="row">{t("ST:ST21")}</th>
                    <td>5 years</td>
                    <td>8%</td>
                  </tr>
                  <tr>
                    <th scope="row">{t("ST:ST22")}</th>
                    <td>3 {t("ST:ST23")}</td>
                    <td>12-14% {t("ST:ST24")}</td>
                  </tr>
                </tbody>
              </table>
              <p>{t("ST:ST25")}</p>
            </div>
            <div className="clearDiv"></div>
          </div>
          <div className="text-center">
            <a
              href="https://app.nivesh.com/home/index"
              target="_blank"
              title=""
              className="btn btn-primary"
            >
              {t('CTAFooter:InvestNow1')}
            </a>
          </div>
        </div>
      </main>
    );
  }
}

export default withTranslation()(savetax);
